import React,{Component} from 'react';
import {connect} from 'react-redux';

class Prev extends Component {
  prevStyle={
    position:"absolute",
    top:"50%",
    left:"25%",
    width:"50px",
    height:"50px",
    fontSize:"30px",
    cursor:"pointer",
  }
  
  constructor(props) {
    super(props);
    this.prev=this.prev.bind(this);
  }
  
  prev() {
    return this.props.dispatch({type:'PREV'});
  }
  
  render() {
    return (
      // <button style={this.prev} onClick={()=>this.props.dispatch({type:'PREV'})}>&lt;</button>
      <button style={this.prevStyle} onClick={this.prev}>&lt;</button>
    );
  }
}

export default connect((state)=>state)(Prev);