import React,{Component} from 'react';
import {connect} from 'react-redux';
import Week from './Week';
import Header from './Header';

class Board extends Component {
  
  render() {
    const weeks=[];
    let weekCount=this.props.weekCount;
    let n=0;
    
    while(weekCount>0) {
      if(weekCount===this.props.weekCount) {
        weeks.push(<Week blank={this.props.firstBlank} count={this.props.weekCount-weekCount} key={n++} />);
        ////第1週目のweekコンポーネント
      }else {
        weeks.push(<Week blank={0} count={this.props.weekCount-weekCount} key={n++} />);
        //第2週目以降ののweekコンポーネント
      }
      weekCount--;
    }
    //weekコンポーネントをweekCount分だけ生成する
    
    return (
      <table>
        <tbody>
          <Header/>
          {weeks}
        </tbody>
      </table>
    );
  }
}

export default connect((state)=>state)(Board);