import React,{Component} from 'react';
import {connect} from 'react-redux';
import Panel from './Panel';


class Week extends Component {
  
  render() {
    const panels=[];
    let blank=this.props.blank;
    let num=0;
    
    for(let i=0;i<7;i++) {
      if(blank>0) {
        panels.push(<Panel panel="" row={i} key={i}/>);
        //1週目の空欄panelコンポーネント
      }else if(this.props.count===0) {
        panels.push(<Panel panel={this.props.dayCount[num]} row={i} key={i}/>);
        num++;
        //1週目の空欄ではないpanelコンポーネント
      }else if(this.props.count*7+this.props.dayCount[num]-this.props.firstBlank<=this.props.lastDay) {
        panels.push(<Panel panel={this.props.count*7+this.props.dayCount[num]-this.props.firstBlank} row={i} key={i}/>);
        num++;
        //2週目以降の空欄ではないpanelコンポーネント
      }
      blank--;
    }
    
    return (
      <tr>{panels}</tr>
    );
  }
}

export default connect((state)=>state)(Week);