import React,{Component} from 'react';
import {connect} from 'react-redux';

class Next extends Component {
  nextStyle={
    position:"absolute",
    top:"50%",
    right:"25%",
    width:"50px",
    height:"50px",
    fontSize:"30px",
    cursor:"pointer",
  }

  constructor(props) {
    super(props);
    this.next=this.next.bind(this);
  }

  next() {
    return this.props.dispatch({type:'NEXT'});
  }

  render() {
    return (
      <button style={this.nextStyle} onClick={this.next}>&gt;</button>
    );
  }
}

export default connect((state)=>state)(Next);