import React,{Component} from 'react';
import {connect} from 'react-redux';

class Panel extends Component {
  td={
    width:"50px",
    height:"50px",
    textAlign:"center",
    cursor:"pointer",
    backgroundColor:"#d1d9e8",
  }
  sunday={
    width:"50px",
    height:"50px",
    textAlign:"center",
    cursor:"pointer",
    backgroundColor:"tomato",
  }
  saturday={
    width:"50px",
    height:"50px",
    textAlign:"center",
    cursor:"pointer",
    backgroundColor:"royalblue",
  }
  
  constructor(props) {
    super();
    console.log(props.row);
  }
  
  render() {
    if(this.props.row===0) {
      return (
        <td style={this.sunday}>{this.props.panel}</td>
      );
    }else if(this.props.row===6) {
      return (
        <td style={this.saturday}>{this.props.panel}</td>
      );
    }else {
      return (
        <td style={this.td}>{this.props.panel}</td>
      );
    }
  }
}

export default connect((state)=>state)(Panel);